import { Component, OnInit, ViewChild } from '@angular/core';

import { AuthService } from './auth/auth.service';
import { MenuItem, AuthUserService } from './auth/auth.user.service';
import { HubMessageComponent } from './utils/hub-message/hub-message.component';
import { Router, NavigationStart } from '@angular/router';
import { User } from 'oidc-client';
import { ThfToolbarAction } from '@totvs/thf-ui/components/thf-toolbar';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
    public showComponent: boolean = true;
    public redictUrl = localStorage['redirect_url'];
    public isLoggedIn: boolean = false;
    public title: string = '';
    public menus: MenuItem[] = [
        { label: 'Tenant', link: 'tenant', permission: 'Management.Tenant', action: '' },
        { label: 'Organização', link: 'organizationUnit', permission: 'Management.Organization', action: '' },
        { label: 'Roles', link: 'role', permission: 'Management.Role', action: '' },
        { label: 'Usuários', link: 'user', permission: 'Management.User', action: '' }
    ];
    public thfMenus: MenuItem[] = [
        { label: 'Inicio', link: 'home', permission: '', action: '' }
    ];
    public user: User;
    public userActions: Array<ThfToolbarAction> = [
        { label: 'Sair', action: 'logout' }
    ];

    @ViewChild(HubMessageComponent) messages: HubMessageComponent;

    constructor(private authService: AuthService, private authUserService: AuthUserService, private router: Router) {
        router.events.forEach((event) => {
            if (event instanceof NavigationStart) {
                this.showComponent = event.url === "" || event.url === "/" || event.url.startsWith("/auth-callback");
            }
        });
    }

    ngOnInit() {
        hubMessage = this.messages;

        this.isLoggedIn = false;

        this.authService.onCompleteAuthentication().subscribe(user => {
            this.user = user;

            if (this.user) {

                this.authUserService.getUserAuthorizedMenus(this.menus).subscribe(authorizedMenus => {
                    this.isLoggedIn = true;
                    this.thfMenus = this.thfMenus.concat(authorizedMenus);
                });
            }
        });

        this.authService.onAccessTokenExpired().subscribe((expired) => {

            hubMessage.openMessageQuestion('Sua sessão expirou. Deseja realizar o login novamente?', "Sessão expirada")
                .subscribe((confirm) => {
                    if (confirm)
                        this.authService.signoutRedirect();
                });
        });
    }

    redirectUrl() {
        window.location.href = decodeURIComponent(this.redictUrl);
    }

    async logout() {
        await this.authService.logout();
    }
}

/** hubMessage: Referência ao componente de mensagem
 *
 */
export let hubMessage: HubMessageComponent;



